import { Component, OnInit, Input } from '@angular/core';
import { Book } from '../book'
import { GetbookService } from '../getbook.service';
import { Location } from '@angular/common';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-book-modify',
  templateUrl: './book-modify.component.html',
  styleUrls: ['./book-modify.component.css']
})
export class BookModifyComponent implements OnInit {
  book: Book;

  //注入获取图书服务、路由、返回
  constructor(
    private getBookService: GetbookService,
    private location: Location,
    private route: ActivatedRoute,
  ) { }

  //选择在 ngOnInit 生命周期钩子中调用 getBookid()
  //之后 Angular 会在构造出 BookModifyComponent 的实例之后的某个合适的时机调用 ngOnInit()。
  ngOnInit(): void {
    this.getBookid();
  }

  //通过路由传过来的图书id，再通过服务获取此id图书详情
  getBookid(): void {
    const id = this.route.snapshot.paramMap.get('id');
    this.getBookService.getBookById(id)
      .subscribe(sbook => this.book = sbook);
  }

  //保存修改后图书
  save(): void {
    if (!this.book.bookname) {
      window.alert('名称不能为空')
      return;
    }
    this.getBookService.updateBook(this.book)
      .subscribe(() => this.goBack());
  }

  //返回上一个位置
  goBack(): void {
    this.location.back();
  }
}
