import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { BookAddComponent } from './book-add/book-add.component';
import { BookDetailComponent } from './book-detail/book-detail.component';
import { BookFindComponent } from './book-find/book-find.component';
import { BookListComponent } from './book-list/book-list.component';
import { BookModifyComponent } from './book-modify/book-modify.component';

const routes: Routes = [
  { path: 'book-list', component: BookListComponent },
  { path: 'book-add', component: BookAddComponent },
  { path: 'book-modify/:id', component: BookModifyComponent },
  { path: 'book-find', component: BookFindComponent },
  { path: 'book-detail/:id', component: BookDetailComponent },
  { path: '', redirectTo: '/book-list', pathMatch: 'full' }
  
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
