import { Component, OnInit } from '@angular/core';
import { Book } from '../book';
import { GetbookService } from '../getbook.service';
import { Location } from '@angular/common';

@Component({
  selector: 'app-book-add',
  templateUrl: './book-add.component.html',
  styleUrls: ['./book-add.component.css']
})
export class BookAddComponent implements OnInit {
  books: Book[];
  book:Book;
  
  infor = '';  

  //注入获取图书服务、返回
  constructor(private getBookService: GetbookService, private location: Location) { }

  ngOnInit(): void {
  }
  
  //增加图书
  add(bookname: string, author: string, publisher: string, introduction: string): void {
    
    //去除输入空格
    bookname = bookname.trim();
    author = author.trim();
    publisher = publisher.trim();
    introduction = introduction.trim();
    //判断名称是否为空并给出提示信息
    if(!bookname){
      window.alert('名称不能为空')
      return;
    }

    this.getBookService.addBook({bookname, author, publisher, introduction } as Book)
      .subscribe(sbook => this.book = sbook);

    
    //给变量赋值显示
    this.infor='增加图书成功，点击查看图书详情'

  }

  //返回函数
  goBack(): void {
    this.location.back();
  }
}


