import { Component, OnInit } from '@angular/core';
import { GetbookService } from '../getbook.service';
import { Book } from '../book'



@Component({
  selector: 'app-book-find',
  templateUrl: './book-find.component.html',
  styleUrls: ['./book-find.component.css']
})
export class BookFindComponent implements OnInit {
  books: Book[];
  book: Book;
  num: number;//搜索结果个数
  booksid = [];//存放搜索结果id
  booksname = [];//存放搜索结果name

  constructor(private getBookService: GetbookService) { }

  //选择在 ngOnInit 生命周期钩子中调用 getAllBooks()
  //之后 Angular 会在构造出 BookListComponent 的实例之后的某个合适的时机调用 ngOnInit()。
  ngOnInit(): void {
    this.getAllBooks();
  }

  //搜索函数
  search(name: string): void {
    this.booksid = [];
    this.booksname = [];
    //删除头尾空白
    name = name.trim();
    if (!name) {
      window.alert('请输入图书名称')
      return;
    }
    //循环比较名称，如果有相同就把id和name拿出来
    for (var i = 0; i < this.books.length; i++) {
      if (this.books[i].bookname.match(name)) {
        this.booksid.push(this.books[i].id);
        this.booksname.push(this.books[i].bookname);
      }
    }
    this.num = this.booksid.length;
  }

  //获取全部图书
  getAllBooks(): void {
    this.getBookService.getBooks()
      .subscribe(sbook => this.books = sbook);
  }
}
